<template>
  <div class="text-white">
    <div>
      <span
        v-for="(command, index) in enteredCommands"
        :key="index"
      >
        <p class="text-white">
          {{ command }}
        </p>
      </span>
    </div>
    <span>
      user@user ~$ 
      <input 
        class="terminal-input text-white" 
        v-model="terminalInput"
        @keyup.enter="onEnter"
      />
    </span>
  </div>
</template>

<script>
import { ref } from "vue"

export default {
  name: "TerminalRow",
  setup() {
    const terminalInput = ref()
    const enteredCommands = ref([])

    function onEnter() {
      enteredCommands.value.push(terminalInput.value)
      terminalInput.value = ""
    }


    return {
      terminalInput,
      enteredCommands,

      onEnter,
    }
  }
}

</script>

<style>
.terminal-input {
  -webkit-appearance: none;
  outline: none;
  background-color: #262626;
  border: none !important;
  border-color: transparent !important;
}
</style>
