<template>
  <div class="terminal-green">
    <ul id="loading-list">
      <li 
        v-for="(text, index) in loadingTexts" 
        :key="index"
      >
        <span> <b>[  <span class="terminal-green">OK</span>  ]</b> {{text}}</span>
      </li>
    </ul>
    <h3 v-if="textsDone">Loading {{ loadingAmount }}</h3>
  </div>
</template>

<script>
import { ref, onMounted } from "vue"

export default {
  name: "LoadingScreen",
  emits: ["ready"],
  setup(props, ctx) {
    const loadingAmount = ref("") // Loading ... text
    const loadingTexts = ref([]) // Displayed ok texts
    const okTexts = ref([ // [ OK ] Loading texts like in linux boot
      "Vue 3 application",
      "Components and views",
      "Cascading style sheets",
      "Monospace vga 8 fonts",
      "Virutal monitor screen ",
      "Mobile version",
      "Command line emulator",
      "Presentation image",
      "Personal portfolio",
      "Contact information",
      "Tools and languages",
      "About me ;)",
    ])
    const textsDone = ref(false)



    async function loading() {
      await loadingText()
      await loadingTitle()
      // Emit ready
    }
    async function loadingTitle() { // Loading bar that push to about view
      let loading = true;
      let amount = 0;
      let dots = ".";
      while (loading === true) {
        if (amount <= 100) {
          await new Promise(r => setTimeout(r, 450));
          amount = amount*2 + 5;
          dots = dots + "......";
        } else {
          loading = false;
          amount = 100;
        }
        loadingAmount.value = `${amount}% ${dots}`;
      }
    }
    async function loadingText() {
      for (let text of okTexts.value) {
        loadingTexts.value.push(text)
        await new Promise(r => setTimeout(r, 150));
      }
      textsDone.value = true
    }

    onMounted(async () => {
      await loading()
      ctx.emit("ready")
    })

    return {
      loadingAmount,
      loadingTexts,
      textsDone,
      okTexts,
    }
  }
}
</script>
