    <div class="custom-select">
      <select class="right" v-model="$i18n.locale">
        <option v-for="locale in $i18n.availableLocales" :key="`locale-en-US`" :value="locale">{{ locale }}</option>
      </select>
    </div>
    <AboutMeRow 
      class="row-comp"
    />
    <TerminalRow 
      class="row-comp"
    />
  </div>
</template>


<script>
import { ref } from "vue"

import LoadingScreen from "@/components/LoadingScreen"
import AboutMeRow from "@/components/AboutMeRow"
import TerminalRow from "@/components/TerminalRow"

export default {
  name: "App",
  components: {
    LoadingScreen,
    AboutMeRow,
    TerminalRow,
  },
  setup() {
    const isLoadingScreenActive = ref(true)

    function hideLoadingScreen() {
      isLoadingScreenActive.value = false
    }
    
    return {
      isLoadingScreenActive,

      hideLoadingScreen,
    }
  }
}
</script>

